<script>
    import axios from 'axios';
    export default {
        name: 'Worldwide',
        data() {
            return {
                totalstats: {},
                countrydata: {},
                selecteddata: {},
                isActive: false,
                popactive: false,
                search: ""
            }
        },
        created() {
            /////////////////////////// 
            //API call for list of vms 
            ////////////////////////// 
            const tokenurl = 'https://corona.lmao.ninja/all';
            axios({
                    method: 'get',
                    url: tokenurl,
                })
                .then((response) => {
                    this.totalstats = response.data;
                })
                .catch((error) => {
                    this.errors = error;
                })


            const url = 'https://corona.lmao.ninja/countries?sort=country';
            axios({
                    method: 'get',
                    url: url,
                })
                .then((response) => {
                    this.countrydata = response.data;
                })
                .catch((error) => {
                    this.errors = error;
                })
        },
        computed:{
            filteredList() {
                return this.countrydata.filter(country => {
                    return country.country.toLowerCase().includes(this.search.toLowerCase())
                })
            
            },
        },

        mounted() {
            this.showData();
        },
        methods: {
            showData() {
                setTimeout(() => {
                    this.isActive = !this.isActive;
                }, 1000)
            },
            getData(cname) {

                const counturl = 'https://corona.lmao.ninja/countries/' + cname;
                axios({
                        method: 'get',
                        url: counturl,
                    })
                    .then((response) => {
                        if (!this.popactive) {
                            this.alertDisplay();
                        }
                        this.selecteddata = response.data;
                        this.popactive = true;
                    })
                    .catch((error) => {
                        this.errors = error;
                    })
            },
            alertDisplay() {
                this.$swal.fire({
                    position: 'center',
                    title: 'Please wait fetching latest data.',
                    showConfirmButton: false,
                    timer: 2000,
                    timerProgressBar: true,
                })
                this.$swal.showLoading();
            }
        }
    }
</script>

